import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    redirect: 'index',
    children: [
      {
        path: '/Shop',
        name: 'Shop',
        component: () => import('../components/Home/shopMain.vue')
      },
      {
        path: '/List',
        name: 'List',
        component: () => import('../components/Home/holeMain.vue')
      },
      {
        path: '/index',
        name: 'index',
        component: () => import('../components/Home/homeMain.vue')
      }
    ]
  },
  {
    path: '/Home',
    redirect () {
      return '/'
    }
  },
  {
    path: '/About',
    name: 'About',
    component: () => import('../views/About.vue')
  },
  {
    path: '*',
    name: 'Error',
    component: () => import('../views/Error.vue')
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
